import { useEffect } from "react";
import style from "./StaffStudentProfile.module.css";
import { useNavigate } from "react-router-dom";
import { assestsUtilityUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
const GoBack = ({ customStyle }) => {
  const navigate = useNavigate();
  const { t } = useTranslation();
  useEffect(() => {
    document.getElementById("scrollView")?.scrollIntoView({
      behavior: "smooth",
      block: "end",
      inline: "nearest",
    });
  }, []);
  const onGoback = () => {
    navigate(-1);
  };
  return (
    <div className={style.goback_container} id="scrollView" style={customStyle}>
      <div className={style.goback_container_text} onClick={onGoback}>
        <img src={`${assestsUtilityUrl}/go-back.svg`} alt="go back icon" />
        <h6 className={style.goback_container_text_go}>{t("go_back")}</h6>
      </div>
    </div>
  );
};

export default GoBack;
