import { useLocation, useSearchParams } from "react-router-dom";
import InstituteNavbarConfig from "../Config/InstituteNavbarConfig";
import InstituteMemberConfig from "../Config/InstituteMemberConfig";
import GoBack from "../../Shared/StaffStudentProfile/GoBack";
import { Suspense, useEffect } from "react";
import QvipleLoader from "../../Loader/QvipleLoader";
import DepartmentStudentFormSetting from "./FormSetting/DepartmentStudentFormSetting";
import { useDepartmentProfile } from "../../../hooks/member_tab/department-api";

const InstituteDepartment = () => {
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const { departmentProfile, departmentProfileRefetch } = useDepartmentProfile({
    did: getQuery.state?.did,
    skip: !getQuery.state?.did,
  });
  useEffect(() => {
    if (getQuery.state?.did) {
      departmentProfileRefetch();
    }
  }, [getQuery.state?.did, departmentProfileRefetch]);
  return (
    <InstituteNavbarConfig>
      <InstituteMemberConfig>
        <section
          style={{
            width: "100%",
          }}
        >
          <GoBack
            customStyle={{
              width: "100%",
              marginTop: "0",
              marginBottom: "1rem",
            }}
          />
          <Suspense fallback={<QvipleLoader />}>
            {searchParams.get("type") === "form" ? (
              <DepartmentStudentFormSetting
                formId={departmentProfile?.department?.student_form_setting}
                departmentId={getQuery.state?.did}
              />
            ) : null}
          </Suspense>
        </section>
      </InstituteMemberConfig>
    </InstituteNavbarConfig>
  );
};

export default InstituteDepartment;
