import { useTranslation } from "react-i18next";
import style from "../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import { useEffect, useRef, useState } from "react";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import DepartmentStudentFormSettingCard from "./DepartmentStudentFormSettingCard";
import {
  useDepartmentStudentFormDetail,
  useDepartmentStudentRearrangeSection,
} from "../../../Hooks/ApiHooks/InstituteStudent/institute-student-api";
import CreateButton from "../../../Ui/Button/CreateButton";
import AddDepartmentFormSection from "./FormSection/AddDepartmentFormSection";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";

const DepartmentStudentFormSetting = ({ formId, departmentId }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const [resuffle, setResuffle] = useState(false);
  const [list, setList] = useState([]);
  const [disabled, setDisabled] = useState(false);
  const dragItem = useRef();
  const dragOverItem = useRef();

  const onDragEventStart = (e, position) => {
    dragItem.current = position;
  };

  const onDragEventEnter = (e, position) => {
    dragOverItem.current = position;
  };
  const onDropItem = (e) => {
    const listCopy = [...list];
    const dragItemContentCls = listCopy[dragItem.current];
    listCopy.splice(dragItem.current, 1);
    listCopy.splice(dragOverItem.current, 0, dragItemContentCls);

    dragItem.current = null;
    dragOverItem.current = null;
    setList(listCopy);
  };

  const [departmentStudentRearrangeSection] =
    useDepartmentStudentRearrangeSection();
  const {
    departmentStudentFormDetail,
    departmentStudentFormDetailRefetch,
    departmentStudentFormDetailLoading,
  } = useDepartmentStudentFormDetail({
    data: {
      formId: formId,
    },
    skip: !formId,
  });

  useEffect(() => {
    if (departmentStudentFormDetail) {
      setList(departmentStudentFormDetail);
    }
  }, [departmentStudentFormDetail]);
  //   console.info("departmentStudentFormDetail", departmentStudentFormDetail);
  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  const onResuffleToggle = () => {
    setResuffle((pre) => !pre);
  };
  const onReshuffle = () => {
    if (formId) {
      setDisabled((pre) => !pre);
      departmentStudentRearrangeSection({
        formId: formId,
        bodyParameter: {
          shuffle_arr: list,
        },
      })
        .then(() => {
          departmentStudentFormDetailRefetch();
          setDisabled((pre) => !pre);
          setResuffle(false);
        })
        .catch({});
    }
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("student_form")}</h6>
            <div className={style.fm_flex}>
              <CreateButton
                label="add_section"
                customStyle={{
                  paddingBlock: "0.2rem",
                  margin: "0",
                }}
                onAction={onToggle}
              />
              <CreateButton
                label={resuffle ? "save_resuffle" : "resuffle"}
                customStyle={{
                  paddingBlock: "0.2rem",
                  margin: "0",
                }}
                onAction={resuffle ? onReshuffle : onResuffleToggle}
              />
              {resuffle && (
                <CreateButton
                  label={"cancel_resuffle"}
                  customStyle={{
                    paddingBlock: "0.2rem",
                    margin: "0",
                  }}
                  onAction={onResuffleToggle}
                />
              )}
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {list?.map((st, index) => (
            <div
              key={st?._id}
              draggable={resuffle}
              onDragStart={(e) => onDragEventStart(e, index)}
              onDragEnter={(e) => onDragEventEnter(e, index)}
              onDragEnd={onDropItem}
            >
              <DepartmentStudentFormSettingCard
                st={st}
                onRefetch={departmentStudentFormDetailRefetch}
                formId={formId}
                departmentId={departmentId}
              />
            </div>
          ))}
          {departmentStudentFormDetailLoading && <Loading />}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {disabled && <QvipleLoader />}
      {add && (
        <AddDepartmentFormSection
          onClose={onToggle}
          formId={formId}
          onRefetch={departmentStudentFormDetailRefetch}
        />
      )}
    </>
  );
};

export default DepartmentStudentFormSetting;
